



import "./index.scss";
import { history } from '&/history';
import { axios } from '@/utils';
import UploadImg from '../../../components/uploadImg';
import { NavBar, List, Grid } from 'antd-mobile';
const Item = List.Item;
const Brief = Item.Brief;
const data = [
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-youhuiquan"></i>),
        text: "优惠券"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-qiapianshuliang"></i>),
        text: "权益卡"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-yue"></i>),
        text: "余额"
    },
    {
        icon: (<i style={{ fontSize: "30px" }} className="iconfont icon-zuanshi"></i>),
        text: "会员积分"
    },
]
const data1 = [
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-dizhi"></i>),
        text: "收获地址"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-icon-test"></i>),
        text: "生日纪念提醒"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-changyongtubiao-xianxingdaochu-zhuanqu-"></i>),
        text: "我的收藏"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-htmal5icon34"></i>),
        text: "浏览记录"
    },
]
const data2 = [
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-kefu1"></i>),
        text: "联系客服"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-bangzhu1"></i>),
        text: "帮助中心"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-gantanhao"></i>),
        text: "关于花礼"
    },
    {
        icon: (<i style={{ fontSize: "20px" }} className="iconfont icon-shezhi"></i>),
        text: "设置"
    },
]

export default class Mine extends Component {
    constructor() {
        super();
        this.state = {
            banner: [],
            msgBrr: [
                require("@/assets/images/myinfo_pendingpay.png"),
                require("@/assets/images/myinfo_distribution.png"),
                require("@/assets/images/myinfo_evaluation.png"),
            ],
            msg: [
                '待付款',
                '今日配送',
                '待评价',
            ],
            isLogin: false,
            usermsg: "Hi,欢迎来到花礼网"
        }
    }
    gotologin() {
        history.push('/login');
    }
    componentDidMount() {
        console.log(this.props);
        console.log(111);
        if (sessionStorage.isLogin == undefined) {

        } else {
            this.setState({
                isLogin: true,
                usermsg: JSON.parse(sessionStorage.getlginpphone)
            })
        }

    }
    render() {
        return (
            <div className="indexview" style={{ position: 'relative', background: '#e3e6ea' }}>
                <NavBar
                    style={{ background: '#fff', color: "black", borderBottom: '5px solid #e9ecf0' }}
                    mode="dark"
                    onLeftClick={() => history.go(-1)}
                    leftContent={<i className="iconfont icon-zuojiantou8"></i>}
                >个人中心</NavBar>
                <img style={{ width: "100%", height: 'auto' }} src={require("@/assets/images/backgroundv3.png")} />
                {/* <UploadImg></UploadImg> */}
                <div style={{ position: 'absolute', top: '40%', left: '36%' }}>
                    <p style={{ color: '#fff', lineHeight: '30px' }}>{this.state.usermsg}</p>
                    {!this.state.isLogin && <p onClick={(res) => { this.gotologin(res) }}
                        style={{ lineHeight: "30px", borderRadius: '15px', padding: '0 10px', background: '#fff', textAlign: 'center' }}>登录/注册</p>
                    }
                </div>
                <div style={{ width: "100%", position: 'absolute', top: "160px", zIndex: '100' }}>
                    <div style={{ borderRadius: "4px", margin: '10px', background: '#fff', padding: '0 10px' }} >
                        <List className="my-list">
                            <Item
                                arrow="horizontal"
                                extra={'全部订单'}>我的订单</Item>
                        </List>
                        <div style={{ display: 'flex', justifyContent: 'space-around', padding: '4% 0' }}>
                            {
                                this.state.msgBrr.map((item, i) => {
                                    return (
                                        <div key={i} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                            <img src={item} style={{ width: "50%" }} />
                                            <div>{this.state.msg[i]}</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                    <div style={{ borderRadius: "8px", margin: '10px', background: '#fff', padding: '0 10px' }}>
                        <Grid data={data} hasLine={false} />
                        <Grid data={data1} hasLine={false} />
                    </div>
                    <div style={{ borderRadius: "8px", margin: '10px', background: '#fff', padding: '10px', position: 'relative' }}>
                        <div style={{ display: 'table-cell', verticalAlign: 'middle', lineHeight: "40px" }}>
                            <img style={{ width: "40px", float: 'left' }} src="http://img02.hua.com/m/images/wxguanjia-kf.png" alt="" />
                            <span style={{ paddingLeft: '10px' }}>专属秘书|喜棠</span>
                        </div>
                        <div style={{ background: '#fff', borderRadius: '4px', marginTop: '10px', boxShadow: "0px 0px 4px #4444", lineHeight: '26px', padding: '0 10px' }}>Hi，我是您的专属秘书喜棠，添加我微信，我将帮您催单，有需求时，1对1帮您走绿色通道。
                        </div>
                        <div style={{ position: 'absolute', background: '#ff734c', padding: '6px 26px', color: "#fff", top: '20px', right: "10px" }}>
                            加我微信
                        </div>
                        <i className="iconfont icon-shangjiantou2" style={{ color: 'grey', position: 'absolute', top: '47px', left: '20px' }}></i>
                    </div>
                    <div style={{ borderRadius: "8px", margin: '10px', background: '#fff', padding: '0 10px', marginBottom: '60px' }}>
                        <Grid data={data2} hasLine={false} />
                    </div>
                </div>
            </div >
        )
    }
}